import Vue from 'vue'
import VueRouter from 'vue-router'
import FristView from '../views/Frist.vue'
import IntroView from '../views/Intro.vue'
import QnaView from '../views/Qna.vue'
import ResultView from '../views/Result'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name:'FristView',
    component: FristView
  },
  {
    path: '/intro',
    name:'IntroView',
    component: IntroView
  },
  {
    path:'/qna_page',
    name:'QnaView',
    component:QnaView
  },
  {
    path:'/result/:id',
    name:'Result_view',
    component:ResultView
  }
]

const router = new VueRouter({
  // mode: 'history',
  base: 'vue_trip',
  routes
})

export default router
